import * as tf from '@tensorflow/tfjs';
import * as tfvis from '@tensorflow/tfjs-vis';

const img_size = 128;
const channel_n = 12
const hidden_n = 96
const lr = 2e-3

// const fire_rate = 0.5
// const batch_size = 4
// const pool_size = 1024
// const fixed_seed = 123 
